import View from './View.js';

class RegisterView extends View {
  #modal = document.querySelector('.register_modal');
  #form = document.querySelector('.form__register');
  #btnModalClose = document.getElementsByClassName('modal__close__btn')[1];

  #pswInput = document.querySelector('.reg__input__psw');
  #pswChkInput = document.querySelector('.reg__input__psw__check');

  constructor() {
    super();

    this.#addHandlerShowModal();

    /*      */
    this.#pswInput.addEventListener(
      'change',
      this.#validatePassword.bind(this)
    );
    this.#pswChkInput.addEventListener(
      'keyup',
      this.#validatePassword.bind(this)
    );
  }

  /*      */
  #addHandlerShowModal() {
    this.#btnModalClose.addEventListener('click', this.#toggleModal.bind(this));
  }

  #toggleModal() {
    this.#modal.classList.toggle('hidden');
    this.#modal.classList.toggle('fadein');
  }

  /*   submit   */

  /**
   * @description     
   */
  #validatePassword() {
    if (this.#pswInput.value !== this.#pswChkInput.value) {
      //  API setCustomValidity        
      this.#pswChkInput.setCustomValidity('  ');
    } else {
      //     
      this.#pswInput.setCustomValidity('');
      this.#pswChkInput.setCustomValidity('');
    }
  }

  /**
   *
   * @param { function } handler Controller   
   * @description              
   */
  addHandlerCreateAccount(handler) {
    this.#form.addEventListener('submit', function (e) {
      //   this    
      // FormData  form input  ['name', 'value']   .
      const formData = [...new FormData(this)].map(data => data[1]);

      e.preventDefault();
      handler(formData);
    });
  }

  /*    */
  closeModal() {
    //  input value 
    this.#form.reset();
    this.#toggleModal();
  }

  // @override
  toggleButtonSpinner() {
    document.querySelector('.submit__register').classList.toggle('spinner');
  }
}

export default new RegisterView();
