import Swal from 'sweetalert2';

export default class View {
  /*    */
  renderError(message) {
    Swal.fire({
      title: '',
      text: message,
      icon: 'error',
    });
  }
  /*    */
  renderSuccessMessage(message) {
    Swal.fire({
      title: '',
      text: message,
      icon: 'success',
      timer: 2000,
    });
  }

  /*       */
  toggleButtonSpinner() {}
}
